<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <!-- 请选择景区 start -->
          <el-form-item label="" prop="spotId">
            <el-select
              v-model="searchValidateForm.spotId"
              filterable
              placeholder="请选择景区"
              clearable
            >
              <el-option
                v-for="(item, index) in searchScenicOptions"
                :key="index"
                :label="item.SCENIC_SPOT_NAME"
                :value="item.SCENIC_SPOT_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择景区 end -->

          <!-- 请选择日期统计方式 start -->
          <el-form-item label="" prop="selectDateState">
            <el-select
              v-model="selectDateState"
              filterable
              placeholder="请选择日期统计方式"
              @change="onDateChange"
            >
              <el-option
                v-for="(item, index) in dateStateOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择日期统计方式 end -->

          <!-- 输入起止时间 start -->
          <el-form-item label="" prop="searchStartEndDate">
            <el-date-picker
              v-model="searchStartEndDate"
              unlink-panels
              :type="dateType"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
            >
            </el-date-picker>
          </el-form-item>
          <!-- 输入起止时间 end -->
        </el-form>
        <!-- 搜索表单 end -->

        <!-- 搜索 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 搜索 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->

        <!-- 导出 Excel start -->
        <el-button
          type="primary"
          :loading="downloadLoading"
          icon="el-icon-download"
          @click="exportExcel"
          >导出Excel</el-button
        >

        <!-- 导出柱状图 start -->
        <a href="" id="exportReport" download="机器人毛利率">
          <el-button
            type="primary"
            icon="el-icon-camera-solid"
            @click="onExportReport"
            >导出柱状图</el-button
          >
        </a>
        <!-- 导出柱状图 end -->
      </el-header>
      <!-- 显示表格 start -->
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <el-tab-pane label="表格" name="table">
          <el-main>
            <!-- 表格 start -->
            <el-table
              class="table"
              :data="tableData"
              style="width: 100%"
              border
              v-loading="tableLoading"
              :header-cell-style="{
                background: '#f2f2f2',
                color: '#606266',
                height: '38px',
                padding: '0px'
              }"
              :cell-style="{ height: '38px', padding: '0' }"
              :default-sort="{ prop: 'grossProfit', order: 'descending' }"
              @sort-change="onSortChange"
              show-summary
              :summary-method="getSummaries"
            >
              <el-table-column
                v-for="(value, key) in tableFields"
                :key="key"
                :prop="value"
                :label="key"
                align="center"
                min-width="130"
                :sortable="tableSortable(value)"
                :fixed="activeFixed(value)"
              >
                <template slot-scope="scope">
                  <div>
                    {{ tableContentValue(scope.row, value) }}
                  </div>
                </template>
              </el-table-column>
            </el-table>
            <!-- 表格 end -->

            <!-- 导出的表格 start -->
            <!-- 此表格始终保持隐藏 -->
            <el-table
              id="outTable"
              class="table"
              :data="exportTableData"
              style="width: 100%; display: none"
              border
            >
              <el-table-column
                v-for="(value, key) in tableFields"
                :key="key"
                :prop="value"
                :label="key"
                min-width="130"
                align="center"
              >
              </el-table-column>
            </el-table>
            <!-- 导出的表格 end -->
          </el-main>
          <el-footer>
            <!-- 分页 start -->
            <div class="block">
              <el-pagination
                background
                layout="prev, pager, next, total, sizes, jumper"
                :total="total"
                :current-page="currentPage"
                :page-sizes="[10, 20, 30, 40]"
                :page-size="pageSize"
                @current-change="onCurrentChange"
                @size-change="onSizeChange"
              >
              </el-pagination>
            </div>
            <!-- 分页 end -->
          </el-footer>
        </el-tab-pane>
        <el-tab-pane label="图表" name="echarts">
          <div class="my-echarts">
            <div id="myChart"></div>
          </div>
        </el-tab-pane>
      </el-tabs>
      <!-- 显示表格 end -->
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { formatDateTime, formatMonth } from '../utils/utils.js'
export default {
  name: 'SettlementFlow',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (!Number(value)) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (!Number(value)) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      showTable: true, // 是否显示表格
      feeInput: '', // 请输入手续费
      taxInput: '', // 请输入税点
      searchFeeInput: '', // 搜索的手续费
      searchTaxInput: '', // 搜索的税点
      tableFields: {
        //导出Excel表格的表头设置
        时间周期: 'cycleTime',
        景区名称: 'spotName',
        '毛利/元': 'grossProfit',
        '净利润/元': 'netProfit',
        '毛利率/%': 'grossProfitMargin',
        '净利率/%': 'netInterestRate',
        '综合成本/元': 'sumPrice',
        机人配比: 'robotPeopleRatio',
        '交易金额/元': 'transactionprice',
        '运营时长/小时': 'operationDuration',
        '台均毛利/元': 'robotGrossProfit',
        '台均综合成本/元': 'comprehensiveCost',
        '平均单台交易金额/元': 'averagePrice',
        '平均单台运营时长/小时': 'averageTime',
        '毛利同比/%': 'yearOnYear',
        '毛利环比/%': 'monthOnMonth',

        // '流水金额/元': 'runningAmount',
        '机器折旧金额/元': 'cost',
        '运营人员成本/元': 'operateCost',
        '景区营销成本/元': 'spotMarketCost',
        '租金/元': 'rent',
        '维养成本/元': 'maintainCost'
      },
      tableData: [], // 表格数据
      tableTotalData: {}, // 表格合计数据
      tableLoading: false,

      searchCompanyOptions: [], // 搜索的公司选项
      searchScenicOptions: [], // 搜索的景区选项

      robotOptions: [], // 提交时的机器人选项
      companyOptions: [], // 提交时的公司选项

      dateStateOptions: [
        {
          value: '1',
          label: '按年查询'
        },
        {
          value: '2',
          label: '按月查询'
        }
      ], // 日期统计方式选项

      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画
      importLoading: false, // 导入excel按钮的加载动画

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数
      total: 0, // 总页数

      dialogVisible: false, // 是否显示编辑dialog对话框

      dialogState: '', // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'

      selectDateState: '2', // 日期统计方式（年，月，日）
      dateType: 'monthrange', // 日期类型（月，日，年）
      valueFormat: 'yyyy-MM', // 日期格式

      searchValidateForm: {}, // 搜索表单验证对象

      validateForm: {}, // 表单验证对象

      searchStartEndDate: [new Date(), new Date()], // 搜索的起止时间

      companys: [], // 公司列表
      companyState: '', // 当前请选择公司

      scenics: [], // 景区列表
      scenicState: '', // 当前请选择景区

      downloadUrl: '',

      myChart: null, // 左侧饼图echarts实例
      myLineChart: null, // 左侧饼图echarts实例
      markLineData: [], // 标线的数据数组
      option: {}, // 左侧饼图optino
      statisticsStateList: [], // 饼图数据

      dialogTitle: '',

      robotId: '', // 当前点击行的 robotId
      statisticDialogVisible: false, // 设备统计dialgo显示状态

      dispatchTrackList: [], // 调运轨迹列表
      dispatchTrackDialogVisible: false, // 调运轨迹dialgo显示状态
      dispatchTrailCurrentPage: 1, // 调运轨迹当前页数
      dispatchTrailTotal: 0, // 调运轨迹总条数
      dispatchTrailPageSize: 10, // 调运轨迹每页多少条
      currentRobotCode: '', // 当前点击行的机器人编号

      errorRecordsList: [], // 故障记录列表

      sumTotal: 0, // 机器人总数量
      nowsumTotal: 0, // 机器人总在线数量
      saturationTotal: 0, // 机器人总运营饱和度

      activeName: 'table', // tabs 显示状态

      // 搜索表单规则
      searchValidateFormRules: {},

      tableSort: '1,desc', // 表格排序

      // 提交表单规则
      validateFormRules: {
        // 付款金额
        // moneyOut: [
        //   {
        //     validator: checkNumberMethod('付款金额'),
        //     trigger: ['blur', 'change']
        //   }
        // ],
      }
    }
  },
  created() {
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }

    this.getSearchValidateForm()
    this.getCompanyList()
    this.getScenicList()
    this.initTableData()
    this.getTableTotalData()
    // this.getRobotIdList()
  },
  beforeMount() {},
  mounted() {
    this.drawEcharts()
    this.getSpotGrossProfitMarginListStatistical()
  },
  methods: {
    // 获取搜索表单验证对象
    getSearchValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.searchValidateForm = {
        spotId: null // 景区id
      }
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=lijiazhao&password=lijiazhao'
      )
    },

    /**
     * 根据景区ID获取公司列表（搜索时公司列表）
     */
    async getCompanyList(spotId) {
      if (!spotId) {
        spotId = null
      }
      const { data: res } = await this.$http.get(
        '/system/robot_time_statistics/companyList',
        {
          params: {
            spotId: spotId
          }
        }
      )
      if (res.state == '200') {
        console.log(res)
        this.searchCompanyOptions = res.data
        this.companyOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 根据公司ID获取景区列表（搜索时景区列表）
     */
    async getScenicList(companyId) {
      if (!companyId) {
        companyId = null
      }
      // 这里使用的是`签约信息`中的`景区列表`接口
      const { data: res } = await this.$http.get(
        '/system/robot_time_statistics/spotList',
        {
          params: {
            companyId: companyId
          }
        }
      )
      if (res.state == '200') {
        // console.log(res);
        this.searchScenicOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 获取机器人毛利率合计
     */
    async getTableTotalData() {
      var startTime = null
      var endTime = null

      if (this.searchStartEndDate.length !== 0) {
        startTime = formatDateTime(
          this.searchStartEndDate[0],
          this.selectDateState
        )
        endTime = formatDateTime(
          this.searchStartEndDate[1],
          this.selectDateState
        )
      }

      const { data: res } = await this.$http.get(
        '/system/operateGrossProfit/getSpotGrossProfitMarginListStatisticalTotal',
        {
          params: {
            type: this.selectDateState,
            spotId: this.searchValidateForm.spotId || null,
            startData: startTime,
            endData: endTime
          }
        }
      )
      if (res.state == '200') {
        console.log(res)
        this.tableTotalData = res.data
      } else {
        this.$message({
          type: 'error',
          message: '获取机器人毛利率合计失败'
        })
        return
      }
    },

    /**
     * 初始化表格数据
     */
    async initTableData() {
      this.tableLoading = true

      var startTime = null
      var endTime = null

      if (this.searchStartEndDate.length !== 0) {
        startTime = formatDateTime(
          this.searchStartEndDate[0],
          this.selectDateState
        )
        endTime = formatDateTime(
          this.searchStartEndDate[1],
          this.selectDateState
        )
      }

      const { data: res } = await this.$http.get(
        '/system/operateGrossProfit/getSpotGrossProfitMarginList',
        {
          params: {
            ...this.searchValidateForm,
            type: this.selectDateState,
            spotId: this.searchValidateForm.spotId || null,
            startTime,
            endTime,
            sort: this.tableSort,
            pageNum: this.currentPage, // 页码
            pageSize: this.pageSize // 每页条数
          }
        }
      )
      console.log(res)
      if (res.state != 200) {
        this.$message({
          type: 'error',
          message: '请求失败！'
        })
        return
      }
      this.tableData = res.data
      this.total = res.total
      // var type = JSON.parse(res.type)
      // this.sumTotal = type.sum
      // this.nowsumTotal = type.nowsum
      // this.saturationTotal = type.saturation

      this.tableLoading = false

      // var resultData = res.data.concat()
      // resultData.forEach((item, index) => {
      //   if (this.searchVF.type == '2' && item.订单状态 == '实际收入金额') {
      //     resultData.unshift(resultData.splice(index, 1)[0])
      //   } else {
      //     if (item.订单状态 == '已付款') {
      //       resultData.unshift(resultData.splice(index, 1)[0])
      //     }
      //   }
      // })

      // var source = resultData.map((item) => {
      //   return [item.订单状态, item.订单数量]
      // })

      // source.unshift(['order', '已付款订单', '其他订单'])
      // this.addOtherData(source, 5)
      // this.option.dataset.source = source
      // this.option.series[0].markLine.data = this.markLineData
      // this.myChart.setOption(this.option)
    },

    /**
     * 新增表格数据
     */
    async addTableData() {
      const { data: res } = await this.$http.post(
        '/system/robotSoftAssetInformation/addRobotSoftAssetInformation',
        this.validateForm
      )
      if (res.state == '200') {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
        this.getTableTotalData()
        this.getSpotGrossProfitMarginListStatistical()
        this.$refs.validateForm.resetFields() // 重置表单
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 修改表格数据
     */
    async editTableData() {
      const { data: res } = await this.$http.post(
        '/system/robotSoftAssetInformation/editRobotSoftAssetInformation',
        this.validateForm
      )
      if (res.state == '200') {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
        this.getTableTotalData()
        this.getSpotGrossProfitMarginListStatistical()
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 删除表格数据
     */
    async delTableData(softAssetInformationId) {
      console.log(softAssetInformationId)
      const { data: res } = await this.$http.delete(
        '/system/robotSoftAssetInformation/delRobotSoftAssetInformation',
        {
          params: {
            softAssetInformationId
          }
        }
      )
      if (res.state == '200') {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
        this.getTableTotalData()
        this.getSpotGrossProfitMarginListStatistical()
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 导出Excel表格的全部数据
     */
    async getExportTableData() {
      const { data: res } = await this.$http.post(
        '/system/robotSoftAssetInformation/uploadExcelRobotSoftAssetInformation',
        {},
        {
          responseType: 'arraybuffer'
        }
      )
      // console.log(res)
      const url = window.URL.createObjectURL(
        new Blob([res], {
          type:
            'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
        })
      )
      const a = document.createElement('a')
      console.log(url)
      a.href = url
      a.download = '机器人毛利率'
      a.click()
      this.downloadLoading = false
    },

    /**
     * 获取机器人id列表
     */
    async getRobotIdList() {
      const { data: res } = await this.$http.get('/system/robot/getRobotIdList')
      // console.log(res)
      if (res.state != 200) {
        this.$message({
          message: '获取机器人列表失败！',
          type: 'error'
        })
        return
      }
      this.robotOptions = res.data
    },

    // 查询机器人毛利率统计图数据
    async getSpotGrossProfitMarginListStatistical() {
      var startData = null
      var endData = null

      if (this.searchStartEndDate.length !== 0) {
        startData = formatDateTime(
          this.searchStartEndDate[0],
          this.selectDateState
        )
        endData = formatDateTime(
          this.searchStartEndDate[1],
          this.selectDateState
        )
      }

      // 加载Echarts loading效果
      this.myChart.showLoading({
        text: 'loading',
        color: '#c23531',
        textColor: '#000',
        maskColor: 'rgba(255, 255, 255, 0.2)',
        zlevel: 0
      })

      const { data: res } = await this.$http.get(
        '/system/operateGrossProfit/getSpotGrossProfitMarginListStatistical',
        {
          params: {
            ...this.searchValidateForm,
            spotId: this.searchValidateForm.spotId || null,
            type: this.selectDateState,
            startData,
            endData
          }
        }
      )
      // console.log(res);
      if (res.state != 200) {
        this.$message({
          message: '获取机器人列表失败！',
          type: 'error'
        })
        return
      }

      var data = res.data || {}
      this.option.xAxis.data = data.spotNameList || []

      this.option.series[0].data =
        data.grossProfitList.map((item) => ((item || 0) * 1).toFixed(2)) || []

      this.option.series[1].data =
        data.yearOnYearList.map((item) => ((item || 0) * 100).toFixed(2)) || []

      this.option.series[2].data =
        data.monthOnMonthList.map((item) => ((item || 0) * 100).toFixed(2)) ||
        []

      this.myChart.setOption(this.option)

      this.myChart.hideLoading()
    },

    /** 搜索表单中选项变化方法 start */
    // 公司选项变化时触发
    async onChangeSearchCompany(companyId) {
      var res = await this.getScenicList(companyId)
      var searchScenicOptions = res.data

      this.searchScenicOptions = searchScenicOptions

      // 如果返回的景区列表为空，则直接返回
      if (searchScenicOptions.length == 0) {
        // this.$message({
        //   type: "info",
        //   message: "/system/subscriptionInformation/scenicList 接口返回的景区列表为空"
        // })
        this.searchValidateForm.spotId = ''
        return
      }

      var isHasScenic = false
      searchScenicOptions.forEach((item) => {
        if (item.SCENIC_SPOT_ID == this.searchValidateForm.spotId) {
          isHasScenic = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则景区，则为搜索景区选择框设置默认值
      var spotId = searchScenicOptions[0].SCENIC_SPOT_ID /* 获取第一个景区id */
      if (!!companyId && !isHasScenic) {
        this.searchValidateForm.spotId = spotId
      }
    },

    // 景区选项变化时触发
    async onChangeSearchSpot(spotId) {
      var res = await this.getCompanyList(spotId)
      // console.log(res.data);
      var searchCompanyOptions = res.data

      // this.searchCompanyOptions = searchCompanyOptions

      // 如果返回的公司列表为空，则直接返回
      if (searchCompanyOptions.length == 0) {
        // this.$message({
        //   type: "info",
        //   message: "/system/subscriptionInformation/companyList 接口返回的公司列表为空"
        // })
        this.searchValidateForm.ascriptionCompanyId = ''
        return
      }

      var isHasCompany = false
      searchCompanyOptions.forEach((item) => {
        if (item.COMPANY_ID == this.searchValidateForm.ascriptionCompanyId) {
          isHasCompany = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则公司，则为搜索公司选择框设置默认值
      if (!!spotId && !isHasCompany) {
        this.searchValidateForm.ascriptionCompanyId =
          searchCompanyOptions[0].COMPANY_ID
      }
    },

    // onChangeSearchType(type) {
    //   console.log(type)
    //   if (type == 1) {
    //     // 按订单数量统计
    //     this.tableFields = {
    //       订单状态: '订单状态',
    //       订单数量: '订单数量',
    //       '订单计费金额/元': '订单计费金额',
    //       '退款金额/元': '退款金额',
    //       '实际收入金额/元': '实际收入金额',
    //       '最终入账金额/元': '最终入账金额'
    //     }
    //   } else if (type == 2) {
    //     // 按订单计费金额统计
    //     this.tableFields = {
    //       //导出Excel表格的表头设置
    //       订单状态: '订单状态',
    //       订单数量: '订单数量',
    //       '订单计费金额/元': '订单计费金额'
    //     }
    //   }
    // },
    /** 搜索表单中选项变化方法 end */

    // 重置
    onReset() {
      // 重新获取表单
      this.getSearchValidateForm()
      this.currentPage = 1

      this.selectDateState = '2'
      this.dateType = 'monthrange'
      this.searchStartEndDate = [new Date(), new Date()]

      // 重新获取表格数据
      this.initTableData()
      this.getTableTotalData()
      this.getSpotGrossProfitMarginListStatistical()
      // 重新获取搜索公司列表
      this.getCompanyList()
      // 重新获取搜索景区列表
      this.getScenicList()
    },

    // 导出报表（导出柱状图）
    onExportReport() {
      this.$nextTick(() => {
        var exportReportEle = document.getElementById('exportReport')
        exportReportEle.href = this.myChart.getDataURL()
        // exportReportEle.click()
      })
    },

    // 导出报表（导出折线图）
    onExportReportLine() {
      this.$nextTick(() => {
        var exportReportEle = document.getElementById('exportReportLine')
        exportReportEle.href = this.myLineChart.getDataURL()
        console.log(exportReportEle)
        // exportReportEle.click()
      })
    },

    // 点击查询数据按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.showTable = true
          // console.log(this.searchValidateForm);
          this.initTableData()
          this.getTableTotalData()
          this.getSpotGrossProfitMarginListStatistical()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 接口导出Excel表格
    exportExcel() {
      this.$confirm('确认导出Excel表格?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          this.downloadLoading = true
          await this.getExportTableData()
          this.$message({
            type: 'success',
            message: '已成功导出Excel表格！',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
        .catch(() => {
          this.downloadLoading = false
          this.$message({
            type: 'info',
            message: '已取消导出Excel表格',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // 文件上传之前执行
    handleBeforeUpload(file) {
      this.importLoading = true
    },

    // 文件上传成功后执行
    handleImportSuccess() {
      this.importLoading = false
      this.$message({
        type: 'success',
        message: '已成功导入Excel表格！',
        offset: 100,
        center: true,
        duration: 1000
      })
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData()
      this.getSpotGrossProfitMarginListStatistical()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
      this.getSpotGrossProfitMarginListStatistical()
    },

    // 搜索表单设备状态改变触发
    onChangeSearchEquipmentStatus(type) {},

    // 提交表单设备状态改变触发
    onChangeEquipmentStatus(type) {
      console.log(type)
    },

    // 提交表单机器人编号改变触发
    onChangeRobotId(robotId) {
      console.log(robotId)
    },

    // 提交表单公司改变触发
    onChangeCompany(companyId) {
      console.log(companyId)
    },

    // 点击关闭设备统计dialog时触发
    handleCloseStatistic() {
      this.statisticDialogVisible = false
    },

    // 切换 tab 时触发
    handleTabClick(tab, event) {
      console.log(tab, event)
    },

    /**
     * 画Echarts图表(柱状图)
     */
    drawEcharts() {
      this.myChart = this.$echarts.init(document.getElementById('myChart'))

      this.option = {
        tooltip: {
          trigger: 'axis',
          textStyle: {
            align: 'left'
          },
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        legend: {
          data: ['毛利', '同比', '环比'],
          top: 0 // 把legend放到柱状图上面
        },
        dataZoom: [
          {
            type: 'inside',
            start: 0,
            end: 10
          },
          {
            type: 'slider'
            // show: true,
            // start: dataAxis.length - 6,
            // end: dataAxis.length - 1,
          }
        ],
        xAxis: {
          type: 'category',
          data: [],
          axisPointer: {
            type: 'shadow'
          }
        },
        yAxis: [
          {
            name: '毛利',
            type: 'value',
            axisLabel: {
              formatter: '{value} 元'
            }
          },
          {
            name: '同比环比',
            type: 'value',
            axisLabel: {
              formatter: '{value} %'
            }
          }
        ],
        series: [
          {
            name: '毛利',
            type: 'bar',
            data: [],
            tooltip: {
              valueFormatter: function(value) {
                return Number(value).toFixed(2) + ' 元'
              }
            }
          },
          {
            name: '同比',
            type: 'line',
            yAxisIndex: 1,
            data: [],
            tooltip: {
              valueFormatter: function(value) {
                return Number(value).toFixed(2) + ' %'
              }
            }
          },
          {
            name: '环比',
            type: 'line',
            yAxisIndex: 1,
            data: [],
            tooltip: {
              valueFormatter: function(value) {
                return Number(value).toFixed(2) + ' %'
              }
            }
          }
        ]
      }

      this.myChart.setOption(this.option)

      window.addEventListener('resize', () => {
        this.myChart.resize()
      })
    },

    // 日期选项值发生变化时触发
    onDateChange() {
      // console.log(this.searchValidateForm.selectDateState);

      if (this.selectDateState == '2') {
        // 按月查询
        this.dateType = 'monthrange'
      } else if (this.selectDateState == '1') {
        // 按年查询
        this.dateType = 'yearrange'
      }
    },

    // 当表格的排序条件发生变化的时候回触发改事件
    onSortChange({ column, prop, order }) {
      console.log(column, prop, order)
      if (prop == 'grossProfit') {
        // 毛利
        if (order == 'descending') {
          this.tableSort = '1,desc'
        } else if (order == 'ascending') {
          this.tableSort = '1,asc'
        } else {
          this.tableSort = ''
        }
      } else if (prop == 'runningAmount') {
        // 流水金额
        if (order == 'descending') {
          this.tableSort = '2,desc'
        } else if (order == 'ascending') {
          this.tableSort = '2,asc'
        } else {
          this.tableSort = ''
        }
      } else if (prop == 'comprehensiveCost') {
        // 台均综合成本
        if (order == 'descending') {
          this.tableSort = '3,desc'
        } else if (order == 'ascending') {
          this.tableSort = '3,asc'
        } else {
          this.tableSort = ''
        }
      } else if (prop == 'robotGrossProfit') {
        // 台均毛利
        if (order == 'descending') {
          this.tableSort = '4,desc'
        } else if (order == 'ascending') {
          this.tableSort = '4,asc'
        } else {
          this.tableSort = ''
        }
      } else if (prop == 'netProfit') {
        // 净利润
        if (order == 'descending') {
          this.tableSort = '5,desc'
        } else if (order == 'ascending') {
          this.tableSort = '5,asc'
        } else {
          this.tableSort = ''
        }
      }

      this.initTableData()
      this.getSpotGrossProfitMarginListStatistical()
    },

    /**
     * 自定义合计行函数
     */
    getSummaries(param) {
      const { columns, data } = param
      const sums = []
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = '合计'
          return
        }
        // console.log(column);

        var { grossProfitAll, netProfitAll } = this.tableTotalData
        // 如果当前列为毛利，则显示
        if (column.property == 'grossProfit') {
          sums[index] = grossProfitAll + '元'
        }

        // 如果当前列为净利润，则显示
        if (column.property == 'netProfit') {
          sums[index] = netProfitAll + '元'
        }
      })

      return sums
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
  },
  computed: {
    // activeFixed() {
    //   return (value) => {
    //     // 冻结左侧“合作主体、景区名称、机器人编号”
    //     if (value == 'SCENIC_SPOT_NAME' || value == 'robotCode') {
    //       return 'left'
    //     }
    //   }
    // }

    tableContentValue() {
      return (row, value) => {
        // console.log(row, value)
        var result = ''
        if (
          value == 'yearOnYear' ||
          value == 'monthOnMonth' ||
          value == 'grossProfitMargin' ||
          value == 'netInterestRate'
        ) {
          result =
            (!isNaN(row[value]) && `${(row[value] * 100).toFixed(2)}%`) ||
            '0.00%'
        } else if (
          value == 'transactionprice' ||
          value == 'cost' ||
          value == 'netProfit' ||
          value == 'comprehensiveCost'
        ) {
          result = (Number(row[value]) && row[value].toFixed(2)) || '0.00'
        } else if (value == 'operationDuration' || value == 'averageTime') {
          result = (Number(row[value]) && row[value].toFixed(2)) || 0
        } else {
          result = row[value] || '0.00'
        }
        return result
      }
    },

    tableSortable() {
      return (value) => {
        var result = ''
        if (
          value == 'runningAmount' ||
          value == 'comprehensiveCost' ||
          value == 'grossProfit' ||
          value == 'robotGrossProfit' ||
          value == 'netProfit'
        ) {
          result = 'custom'
        } else {
          result = false
        }

        return result
      }
    },

    activeFixed() {
      return (value) => {
        // 冻结左侧“时间周期、景区名称、机器人编号”
        if (value == 'cycleTime' || value == 'spotName') {
          return 'left'
        }
      }
    }
  }
}
</script>

